import { ref, set, child, push, update } from "firebase/database";
import { database } from "../firebase";

class DataService {
  /*getAll() {
    const db = ref(database, "/users");
    return db;
  }
  getAllLocks() {
    const db = ref(database, "/locks");
    return db;
  }
  getUserCredentials(lock, user) {
    const db = ref(database, "/locks/" + lock + "/users/" + user);
    return db;
  }
  getUserSchedule(lock, user) {
    const db = ref(database, "/locks/" + lock + "/schedule/" + user);
    return db;
  }*/
  getFromPath(path) {
    const db = ref(database, path);
    return db;
  }
  create(user) {
    const newPostKey = push(child(ref(database), "/users")).key;
    //console.log(user, newPostKey);
    return set(ref(database, "/users/" + newPostKey), {
      name: user.name,
      surname: user.surname,
      id: user.id,
      phone: user.phone,
    });
  }
  createEmployee(employee) {
    const newPostKey = push(child(ref(database), "/employees")).key;
    //console.log(user, newPostKey);
    return set(ref(database, "/employees/" + newPostKey), {
      name: employee.name,
      surname: employee.surname,
      email: employee.email,
      id: employee.id,
      phone: employee.phone,
      role: employee.role,
    });
  }
  update(updates) {
    return update(ref(database), updates);
  }
}
export default new DataService();
