<template>
  <div class="alert" :class="type" role="alert" v-if="show">
    {{ message }}
    <div class="close">
      <button type="button" class="btn button-close" @click="closeMessage">
        <i class="bi bi-x-lg"></i>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "alert-message",
  props: ["show", "message", "closeMessage", "messageType"],
  data() {
    return {
      type: "",
    };
  },
  watch: {
    messageType: function () {
      this.type = "alert-" + this.messageType;
    },
  },
  methods: {},
  mounted() {},
};
</script>

<style scoped>
.alert {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: 1em;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: #00000070 0 0 9px 2px;
  transform: translateY(-100%);
  animation: slideIn 250ms cubic-bezier(0.18, 0.89, 0.32, 1.28) forwards;
}
@keyframes slideIn {
  from {
    opacity: 0;
  }
  to {
    transform: translateY(0%);
    opacity: 1;
  }
}
</style>
