<template>
  <nav v-if="logged" class="navbar bg-dark">
    <div class="links">
      <div class="logo">
        <router-link to="/" class="logo-link">LockManage</router-link>
      </div>
      <ul class="nav">
        <router-link to="/users" class="nav-link" v-if="loggedRole === 'Admin'">
          <li class="item">Users</li>
        </router-link>
        <router-link to="/add" class="nav-link" v-if="loggedRole === 'Admin'">
          <li class="item">Add User</li>
        </router-link>
        <router-link
          to="/addEmployee"
          class="nav-link"
          v-if="loggedRole === 'Admin'"
        >
          <li class="item">Add Employee</li>
        </router-link>
        <router-link
          to="/addLock"
          class="nav-link"
          v-if="loggedRole === 'Admin'"
        >
          <li class="item">Add Lock</li>
        </router-link>
        <router-link to="/sim" class="nav-link" v-if="loggedRole === 'Admin'">
          <li class="item">Simulator</li>
        </router-link>
      </ul>
    </div>
    <div class="buttons">
      <div class="reset">
        <button @click="resetPassword" class="btn btn-secondary">
          Reset password
        </button>
      </div>
      <div class="logout">
        <button @click="logout" class="btn btn-secondary">Logout</button>
      </div>
    </div>
  </nav>
  <Teleport to="body">
    <Message
      :messageType="messageType"
      :show="showMessage"
      :message="messageContent"
      :closeMessage="closeMessage"
    />
  </Teleport>
</template>

<script>
import { auth } from "@/firebase";
import { sendPasswordResetEmail } from "firebase/auth";
import Message from "./Message";

export default {
  name: "nav-bar",
  components: { Message },
  setup() {
    const logout = () => {
      auth.signOut(auth);
      sessionStorage.clear();
    };
    return { logout };
  },
  data() {
    return {
      logged: false,
      loggedRole: "",
      showMessage: false,
      messageType: "",
      messageContent: "",
    };
  },
  methods: {
    printMessage(type, message) {
      this.showMessage = true;
      this.messageType = type;
      this.messageContent = message;
    },
    closeMessage() {
      this.showMessage = false;
    },
    resetPassword() {
      sendPasswordResetEmail(auth, auth.currentUser.email)
        .then(() => {
          this.printMessage(
            "success",
            "Password resent sent, check your mailbox!"
          );
          //alert("Password resent sent, check your mailbox!");
        })
        .catch((e) => {
          console.log(e);
          ("Error occured, report it to administrator!");
        });
    },
  },
  mounted() {
    if (auth.currentUser) {
      this.logged = true;
      this.loggedRole = sessionStorage.getItem("role");
      //console.log(this.loggedRole);
    } else this.logged = false;
  },
};
</script>

<style scoped>
.container h2 {
  text-align: center;
  margin: 25px auto;
}
.nav .nav-link {
  color: #cacaca;
  display: flex;
}
.nav .nav-link:hover {
  background-color: rgb(95, 95, 95);
}
a:hover {
  text-decoration: none;
}
.nav .router-link-active {
  background-color: rgb(95, 95, 95);
}
.links {
  display: flex;
}
.item {
  transition: 250ms background-color ease-in-out;
  display: flex;
  align-items: center;
}
.navbar {
  padding: 0;
  display: flex;
  align-items: stretch;
}
.logo {
  margin: 1rem;
}
.logo-link {
  color: #cacaca;
}
.buttons {
  display: flex;
  align-items: center;
}
.logout,
.reset {
  margin-right: 0.5rem;
}
</style>
