<template>
  <MainLayout>
    <div class="content">
      <div class="wrapper">
        <div class="form">
          <div class="form-group">
            <label for="name">Name</label>
            <input
              type="text"
              class="form-control"
              id="name"
              required
              v-model="lock.name"
              name="name"
            />
          </div>
          <div class="form-group">
            <label for="description">Description (Can be leave empty)</label>
            <input
              type="text"
              class="form-control"
              id="email"
              required
              v-model="lock.description"
              name="email"
            />
          </div>
          <div class="form-group">
            <select v-model="lock.authType">
              <option disabled value="">Select auth type</option>
              <option>c</option>
              <option>c+w</option>
              <option>cw</option>
            </select>
          </div>
          <button @click="saveLock" class="btn btn-success">Save</button>
        </div>
      </div>
    </div>
    <Teleport to="body">
      <Message
        :messageType="messageType"
        :show="showMessage"
        :message="messageContent"
        :closeMessage="closeMessage"
      />
    </Teleport>
  </MainLayout>
</template>
<script>
import DataService from "../services/DataService";
import { onValue } from "firebase/database";
import Message from "./Message";
import MainLayout from "./MainLayout.vue";
export default {
  name: "add-lock",
  components: { MainLayout, Message },
  data() {
    return {
      lock: {
        name: "",
        description: "",
        authType: "",
        authInfo: "",
      },
      alert: "",
      showMessage: false,
      messageType: "",
      messageContent: "",
    };
  },
  methods: {
    printMessage(type, message) {
      this.showMessage = true;
      this.messageType = type;
      this.messageContent = message;
    },
    closeMessage() {
      this.showMessage = false;
    },
    saveLock() {
      if (this.validate()) {
        const update = {};
        update["/locksList/" + this.lock.name] = "Device";
        update["/locks/" + this.lock.name + "/info/authType"] =
          this.lock.authType;
        update["/locks/" + this.lock.name + "/info/authInfo"] =
          this.getAuthInfo(this.lock.authType);
        if (this.lock.description != "") {
          update["/locks/" + this.lock.name + "/info/lockInfo"] =
            this.lock.description;
        }
        DataService.update(update)
          .then(() => {
            this.printMessage("success", this.alert);
            //alert(this.alert);
            this.newLock();
          })
          .catch((e) => {
            this.printMessage(
              "danger",
              "Error occured, report it to administrator!"
            );
            console.log(e);
          });
      } else {
        this.printMessage("warning", this.alert);
        //alert(this.alert)
      }
    },
    getAuthInfo(auth) {
      if (auth == "c") {
        return "Weryfikacja użytkownika za pomocą ID i hasła";
      } else if (auth == "c+w") {
        return "Weryfikacja użytkownika za pomocą ID i hasła, w przypadku próby otworzenia zamka poza harmonogramem wymagana jest autoryzacja osoby trzeciej";
      } else if (auth == "cw") {
        return "Weryfikacja użytkownika za pomocą ID i hasła, potwierdzona przez osobę trzecią. Dostęp wyłącznie w czasie harmonogramu";
      }
    },
    newLock() {
      this.lock = {
        name: "",
        description: "",
        authType: "",
        authInfo: "",
      };
    },
    validate() {
      this.alert = "";
      if (this.lock.name == "") {
        this.alert += "Name cannot be empty. ";
      }
      if (this.nameCheck(this.lock.name)) {
        this.alert += "Name already in use. ";
      }
      if (this.lock.authType == "") {
        this.alert += "Select auth type for lock. ";
      }
      if (this.alert === "") {
        this.alert += "Lock created successfully!";
        return true;
      }
      return false;
    },
    nameCheck(name) {
      let exist;
      onValue(DataService.getFromPath("/locksList/" + name), (snapshot) => {
        if (snapshot.exists()) {
          exist = true;
        } else {
          exist = false;
        }
      });
      return exist;
    },
  },
  mounted() {},
};
</script>
<style scoped>
.content {
  overflow-y: auto;
  display: flex;
}

.wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 1rem;
  padding: 1rem;
  gap: 1rem;
  background-color: #b1b1b1;
  border-radius: 0.5rem;
  overflow-y: auto;
  box-shadow: 0px -1px 10px 4px #141414a6;
}
.form {
  width: 60%;
}
</style>
